"use client";
import { useState } from "react";
import BloomingMap from "./BloomingMap";

export default function Home() {
  const [showCompareModal, setShowCompareModal] = useState(false);
  const [compareMode, setCompareMode] = useState(null);
  const [dateRange, setDateRange] = useState({
    start: "2024-01-01",
    end: "2024-12-31"
  });

  const handleResetCompareMode = () => {
    setCompareMode(null);
  };

  return (
    <main className="w-full h-screen flex flex-row justify-center">
      <div id="side-bar" className="justify-center w-10 bg-primary">
        <img src="./bloom.svg" className="h-10" alt="Bloom logo" />
      </div>
      <div id="navigator" className="absolute flex my-[15px] z-1">
        <div>
          <img src="./flower.svg" className="absolute my-[5px] mx-[25px] w-10 h-[20px]" alt="Flower icon" />
          <button
            id="species"
            className="h-[30px] mx-[20px] px-[40px] w-40 bg-primary font-bold text-white text-left rounded-3xl"
          >
            Species
          </button>
        </div>
        <div>
          <img src="./comparison.svg" className="absolute my-[5px] mx-[25px] w-10 h-[20px]" alt="Comparison icon" />
          <button
            id="locations"
            onClick={() => setShowCompareModal(true)}
            className="h-[30px] mx-[20px] px-[40px] w-40 bg-primary font-bold text-white text-left rounded-3xl"
          >
            Comparison
          </button>
        </div>
      </div>

      {showCompareModal && (
        <div className="absolute inset-0 bg-black/60 flex items-center justify-center z-50">
          <div className="bg-white rounded-2xl p-8 w-[500px] shadow-2xl">
            <div className="flex justify-between items-center mb-6">
              <h2 className="text-2xl font-bold text-gray-800">Choose Comparison Mode</h2>
              <button 
                onClick={() => setShowCompareModal(false)}
                className="text-gray-500 hover:text-gray-700"
              >
                ‚úï
              </button>
            </div>

            <div className="mb-6">
              <label className="block text-sm font-medium text-gray-700 mb-2">Date Range</label>
              <div className="grid grid-cols-2 gap-4">
                <input
                  type="date"
                  value={dateRange.start}
                  onChange={(e) => setDateRange(prev => ({ ...prev, start: e.target.value }))}
                  className="block w-full px-3 py-2 border border-gray-300 rounded-md"
                />
                <input
                  type="date"
                  value={dateRange.end}
                  onChange={(e) => setDateRange(prev => ({ ...prev, end: e.target.value }))}
                  className="block w-full px-3 py-2 border border-gray-300 rounded-md"
                />
              </div>
            </div>

            <div className="flex flex-col gap-4">
              <button
                onClick={() => { 
                  setCompareMode({ type: "global", dateRange }); 
                  setShowCompareModal(false); 
                }}
                className="px-6 py-4 bg-emerald-500 text-white rounded-lg hover:bg-emerald-600 font-semibold transition-colors"
              >
                üåç Global NDVI Comparison
              </button>
              <button
                onClick={() => { 
                  setCompareMode({ type: "point", dateRange }); 
                  setShowCompareModal(false); 
                }}
                className="px-6 py-4 bg-blue-500 text-white rounded-lg hover:bg-blue-600 font-semibold transition-colors"
              >
                üìç Compare Two Points
              </button>
              <button
                onClick={() => { 
                  setCompareMode({ type: "export", dateRange }); 
                  setShowCompareModal(false); 
                }}
                className="px-6 py-4 bg-purple-500 text-white rounded-lg hover:bg-purple-600 font-semibold transition-colors"
              >
                üìä Export Analysis Charts
              </button>
            </div>
          </div>
        </div>
      )}

      <BloomingMap compareMode={compareMode} onResetCompareMode={handleResetCompareMode} />
    </main>
  );
}